:root {
  --bg-color: #f5f5f5;
  --text-color: #333;
  --button-bg: #e0e0e0;
  --button-hover: #d0d0d0;
  --display-bg: #fff;
  --equals-bg: #4a90e2;
  --equals-hover: #3a80d2;
}

.dark {
  --bg-color: #1a1a1a;
  --text-color: #f5f5f5;
  --button-bg: #333;
  --button-hover: #444;
  --display-bg: #222;
  --equals-bg: #2a6fc9;
  --equals-hover: #1a5fb9;
}

.app {
  background-color: var(--bg-color);
  color: var(--text-color);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}

.calculator {
  width: 300px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.display {
  background-color: var(--display-bg);
  padding: 20px;
  text-align: right;
  font-size: 2em;
  border-bottom: 1px solid #ddd;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 5px;
  padding: 10px;
  background-color: var(--bg-color);
}

.button {
  border: none;
  padding: 20px;
  font-size: 1.2em;
  cursor: pointer;
  background-color: var(--button-bg);
  color: var(--text-color);
  border-radius: 5px;
  transition: background-color 0.2s;
}

.button:hover {
  background-color: var(--button-hover);
}

.equals {
  background-color: var(--equals-bg);
  color: white;
  grid-row: span 2;
}

.equals:hover {
  background-color: var(--equals-hover);
}

.zero {
  grid-column: span 2;
}

.theme-toggle {
  background: none;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
  padding: 10px;
  margin-left: auto;
  display: block;
}

.history {
  padding: 10px;
  max-height: 200px;
  overflow-y: auto;
  background-color: var(--display-bg);
  border-top: 1px solid #ddd;
}

.history h3 {
  margin-top: 0;
}

.history ul {
  list-style: none;
  padding: 0;
}

.history li {
  padding: 5px 0;
  border-bottom: 1px solid #eee;
}

@media (max-width: 400px) {
  .calculator {
    width: 100%;
    height: 100vh;
    border-radius: 0;
  }
}